import React from 'react';
import ReactDOM from 'react-dom/client';
import './index.css';
import store from "./redux/store";
import { Provider } from 'react-redux';
import { dragElement } from './helpers/dragElement';
import App from './App';
import "./injection.css"
import ProductWatcher from './ProductWatcher';

/**
 * The container for the extension. The element with the id 'sift-extension-container'
 */
const container = document.createElement("div");
container.id = "sift-extension-container";
document.body.appendChild(container);
document.getElementById("sift-extension-container")!.style.display = "none";
dragElement(document.getElementById("sift-extension-container")!);
const root = ReactDOM.createRoot(
    document.getElementById('sift-extension-container') as HTMLElement
);

/**
 * A draggable icon that can be used to open the extension
 */
const draggableIcon = document.createElement("div");
draggableIcon.id = "draggableicon";
const toggleExtensionContainer = () => {
    if (document.getElementById("sift-extension-container")!.style.display === "none") {
        document.getElementById("sift-extension-container")!.style.display = "block"
    } else {
        document.getElementById("sift-extension-container")!.style.display = "none"
    }
}
document.body.appendChild(draggableIcon);
dragElement(document.getElementById("draggableicon")!);


document.getElementById("draggableicon")!.addEventListener("click", toggleExtensionContainer);

root.render(
    <React.StrictMode>
        <Provider store={store}>
            <ProductWatcher />
            <App />
        </Provider>

    </React.StrictMode>
);

