import { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { LdJsonProcessor } from "./private-modules/LdJsonProcessor/LdJsonProcessor";
import { setRecs, setRecSessionId } from "./redux/slices/recProductSlice";
import { BackendProduct } from "./types/custom-types/BackendProduct";
import { FrontendProduct } from "./types/custom-types/FrontendProduct";

// The ProductWatcher component is responsible for detecting ld+json scripts on the page and sending a message to the background script
export default function ProductWatcher() {
    // Used to dispatch actions to the redux store
    const dispatch = useDispatch();
    // The product that is currently on the page. Used to avoid processing multiple valid ld+json scripts
    const [productOnPage, setProductOnPage] = useState<FrontendProduct | null>(null);
    // Used to avoid sending multiple requests for recommendations (when clicking on the icon)
    const [hasRequested, setHasRequested] = useState(false);

    // Observer detects mutations in the DOM
    const observer = new MutationObserver(newScriptsCallback);

    // Callback for observer. It detects whether there is a new ld+json script. If so, it processes it.
    function newScriptsCallback(mutationsList: MutationRecord[], observer: MutationObserver) {
        const recurse = (parent: Node) => {
            if (parent.nodeName == 'SCRIPT') {
                if ((parent as HTMLElement).getAttribute('type') === 'application/ld+json') {
                    console.log("Mutation Observer: Processing ld+json script");
                    processLdJson(parent as Element);
                }
            }
            if (parent.childNodes) {
                [...parent.childNodes].forEach(recurse);
            }
        };
        for (let mutation of mutationsList) {
            if (mutation.type == 'childList') {
                for (let node of mutation.addedNodes) {
                    recurse(node as HTMLElement);
                }
            }
        }
    }

    // If the ld+json script is a product, we set the product on the page
    function processLdJson(ldJsonScript: Element) {
        const ldJsonProcessor = new LdJsonProcessor(ldJsonScript);
        const mightbeProduct = ldJsonProcessor.handleLdJsonScript();
        if (mightbeProduct != null) {
            setProductOnPage(mightbeProduct);
        }
    }

    // Checks if there is a ld+json script statically on the page
    function watchStaticLdJson() {
        const ldJsonScripts = document.querySelectorAll('script[type="application/ld+json"]');
        for (let ldJsonScript of ldJsonScripts) {
            console.log("Static: Processing ld+json script");
            processLdJson(ldJsonScript);
        }
    }

    // Sends a message to the background script to get recommendations when icon is clicked
    // and sets the recommendations returned from background in the redux store
    const handleClick = () => {
        // if extension is displayed on the page, we don't want to send a request to the backend
        if (document.getElementById("sift-extension-container")!.style.display != "none"){
            if (!hasRequested) {
                setHasRequested(true);
                chrome.runtime.sendMessage({
                    message: "recommend-products",
                    payload: productOnPage,
                    url: window.location.href
                }, function (response) {
                    // dispatch(setRecs([convertFrontendToBackendProduct(productOnPage!)] as BackendProduct[]));
                    const session_id: string = response.sessionId;
                    const products: BackendProduct[] = response.products;
                    if (products) {
                        dispatch(setRecs(products));
                        dispatch(setRecSessionId(session_id));
                    }
                })
            } else {
                console.log("Already have recommendation. Don't send");
            }
        }

    }

    // Attempt to find static ld+json scripts on the page and then initialize the observer
    function initialize() {
        watchStaticLdJson();
        const head = document.head;
        const body = document.body;
        const options = {
            // monitor the target node (and, if subtree is true, its descendants) for the addition of new child nodes or removal of existing child nodes
            childList: true,
            // extend monitoring to the entire subtree of nodes rooted at target
            subtree: true
        };
        observer.observe(body, options);
        observer.observe(head, options);
    };

    // Remove the observer and remove the click event listener for the icon, called when the url changes or the component is unmounted
    function cleanUp() {
        setProductOnPage(null);
        document.getElementById("draggableicon")!.removeEventListener('click', handleClick);
        observer.disconnect();
    }

    /**
     * Effect is called when the product on the page changes. 
     * If there is a product on the page, we 
     * 1. Show the icon
     * 2. Add a click event listener to the icon (for sending recommendation requests and setHasRequested to true)
     * 3. Send a message to the background script to log the visit
     * If there is no product on the page, we
     * 1. Hide the icon and the extension container
     * 2. Clear the recommendations in the redux store
     * 3. Set hasRequested to false
     */
    useEffect(() => {
        if (productOnPage != null) {
            console.log(productOnPage);
            document.getElementById("draggableicon")!.addEventListener("click", handleClick);
            document.getElementById("draggableicon")!.style.display = "block";
            chrome.runtime.sendMessage({
                message: "visit-site",
                payload: {
                    "log_type": "visit_site",
                    "site_url": window.location.href
                },
            });
        } else {
            document.getElementById("draggableicon")!.style.display = "none";
            document.getElementById("sift-extension-container")!.style.display = "none";
            dispatch(setRecs([]));
            setHasRequested(false);
        }
    }, [productOnPage]);


    // initialize the observer when the component is mounted (when page is loaded/refreshed/reloaded)
    useEffect(() => {
        setTimeout(() => {
            initialize();
        }, 100)
        return () => {
            cleanUp();
        }
    }, []);

    // initialize the observer when the url changes
    chrome.runtime.onMessage.addListener(
        function (request, sender, sendResponse) {
            // listen for messages sent from background.js
            if (request.message === 'url-change') {
                cleanUp();
                setTimeout(() => {
                    initialize();
                }, 100)
            }
        });

    return null;
}