import {Box, Typography} from "@mui/material";
import {BackendProduct} from "../../types/custom-types/BackendProduct";

interface FooterProps {
    product: BackendProduct
    showBack: boolean
}

export default function Footer(props: FooterProps) {
    return (
        <Box
            sx={{
                display: 'fixed',
                flexDirection: 'row',
                width: '100%',
                height: '50px',
            }}>
            <Box sx={{width: "2%"}}/>
            <Box
                sx={{
                    width: '38%',
                    height: '100%',
                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "center"
                }}>
                <Typography variant={'h5'}
                            sx={{
                                color: (props.showBack)?"#ffffff":'#7AAEED',
                                //will need to be played around with
                                fontSize: (props.product.price <= 99) ? 25: ((props.product.price <=999) ? 22.5 : 20)
                            }}>
                    {'$' + props.product.price}
                </Typography>

            </Box>
            <Box
                sx={{
                    width: '58%',
                    height: '100%',
                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "center"
                }}>
                <Typography variant={'h6'}
                            sx={{
                                textAlign: 'center',
                                color: (props.showBack)?"#ffffff": '#000000',
                                fontSize: (props.product.website.length <= 8) ? "1rem" : ((props.product.website.length <= 14) ? "0.8rem" : "0.6rem")
                            }}>
                    {props.product.website.toLowerCase()}
                </Typography>

            </Box>
            <Box sx={{width: "2%"}}/>

        </Box>
    )

}