import { FrontendProduct } from "../../types/custom-types/FrontendProduct";
import { Graph, Product, Thing } from "schema-dts";

export class LdJsonProcessor{
    private _script: Element;
    
    constructor(script: Element){
        this._script = script;
    }

    /**
     * It handles one ld+json script. (in other words, the content of one
     * <script type="application/ld+json"/>)
     * @param script the HTML element of the ld+json script
     */
    public handleLdJsonScript(): FrontendProduct | null {
        const jsonObject = JSON.parse(this._script.innerHTML.replace(/(\r\n|\n|\r)/gm, ""));
        if (jsonObject.hasOwnProperty(0)) {
            //iterate through the jsonObject if it is a list
            for (let scriptObj of jsonObject) {
                const mightBeProduct= this.parseJsonObj(scriptObj);
                if(mightBeProduct != null){
                    return mightBeProduct;
                }
            }
            return null;
        } else {
            const mightBeProduct = this.parseJsonObj(jsonObject)
            return mightBeProduct;
        }
    }

    /**
     *  if the given jsonObj is a Thing (as defined in https://github.com/google/schema-dts)
     * @param jsonObj
     */
    private isThing(jsonObj: any): jsonObj is Thing {
        return (jsonObj as Thing).toString() !== undefined;
    }

    /**
     * if the given jsonObj is a Product (as defined in https://github.com/google/schema-dts)
     * @param jsonObj
     */
    private isProduct(jsonObj: any) {
        return (jsonObj as Product)["@type"] === "Product";
    }

    /**
     * if the jsonObj is a graph ld+json object (checking if it contains the
     * attribute "@graph"
     * @param jsonObj
     */
    private isGraph(jsonObj: any): jsonObj is Graph {
        return (jsonObj as Graph)["@graph"] !== undefined;
    }

    /**
     * Extract the product information for a thing (type Thing) if it is
     * a Product.
     * @param thing a Thing to be examined
     */
    private parseThing(thing: Thing): FrontendProduct | null {
        if (!this.isThing(thing)) throw new Error("parseThing not parsing a thing");

        let productName, productDescription, productCategory,
            productImg, productColor, productBrand;

        productName = (thing as Product).name;
        productDescription = (thing as Product).description;
        productCategory = (thing as Product).category;
        productImg = (thing as Product).image;
        productColor = (thing as Product).color;
        productBrand = (thing as Product).brand;

        if (productName == undefined) {
            const ogTitleEle = document.head.querySelector('meta[property="og:title"]');
            const twitterTitleEle = document.querySelector('meta[name="twitter:title"]');
            if (ogTitleEle != null) {
                productName = ogTitleEle.getAttribute('content');
            } else if (twitterTitleEle != null) {
                productName = twitterTitleEle.getAttribute('content');
            }
        }
        if (productDescription == undefined) {
            const ogDescEle = document.head.querySelector('meta[property="og:description"]');
            const twitterDesEle = document.querySelector('meta[name="twitter:description"]');
            if (ogDescEle != null) {
                productDescription = ogDescEle.getAttribute('content');
            } else if (twitterDesEle != null) {
                productDescription = twitterDesEle.getAttribute('content');
            }
        }
        if (productImg == undefined) {
            const ogImgEle = document.head.querySelector('meta[property="og:image"]');
            const twitterImgEle = document.querySelector('meta[name="twitter:image"]');
            if (ogImgEle != null) {
                productImg = ogImgEle.getAttribute('content');
            } else if (twitterImgEle != null) {
                productImg = twitterImgEle.getAttribute('content');
            }
        }

        if (productColor == undefined) {
            const ogImgEle = document.head.querySelector('meta[property="og:image"]');
            const twitterImgEle = document.querySelector('meta[name="twitter:image"]');
            if (ogImgEle != null) {
                productImg = ogImgEle.getAttribute('content');
            } else if (twitterImgEle != null) {
                productImg = twitterImgEle.getAttribute('content');
            }
        }

        if (productBrand == undefined) {
            const ogImgEle = document.head.querySelector('meta[property="og:image"]');
            const twitterImgEle = document.querySelector('meta[name="twitter:image"]');
            if (ogImgEle != null) {
                productImg = ogImgEle.getAttribute('content');
            } else if (twitterImgEle != null) {
                productImg = twitterImgEle.getAttribute('content');
            }
        }

        const scrapedProduct = Object({
            title: productName,
            description: productDescription,
            category_path: productCategory,
            img_url: productImg,
            color: productColor != undefined ? productColor: "",
            brand: productBrand != undefined ? productBrand: ""
        }
        ) as FrontendProduct;
        return scrapedProduct
    }

    /**
     * Handling a ld+json script according to whether if it is a graph, a Thing,
     * or throwing an error if scriptObj is neither a grpah or a Thing.
     * @param scriptObj ld+json script object
     * @throws Error if the ld+json script object is neither a graph or a Thing
     */
    private parseJsonObj(scriptObj: any): FrontendProduct | null {
        if (this.isGraph(scriptObj)) {
            for (let jsonNode of (scriptObj as Graph)["@graph"]) {
                if (this.isProduct(jsonNode)) {
                    const mightBeProduct = this.parseThing(jsonNode);
                    if(mightBeProduct != null){
                        return mightBeProduct;
                    }
                } 
            }
            return null;
        } else if (this.isThing(scriptObj)) {
            if (this.isProduct(scriptObj)) {
                return this.parseThing(scriptObj);
            }
            return null;
        } else {
            console.log("Error parsing ld+json script");
            return null;
        }
    }


    

}