import InitialState, { UpdateRecProductsAction } from '../../types/redux/recProduct';
import { createSlice, PayloadAction } from "@reduxjs/toolkit";
import { BackendProduct } from "../../types/custom-types/BackendProduct";

const initialState: InitialState = {
    products: [],
    recSessionId: ""
};

export const recProductSlice = createSlice({
    name: UpdateRecProductsAction,
    initialState: initialState,
    reducers: {
        pushToRecs: (state, action: PayloadAction<BackendProduct>) => {
            state.products.push(action.payload);
        },
        popFromRecs: (state, action: PayloadAction<BackendProduct>) => {
            delete state.products[state.products.indexOf(action.payload)];
        },
        setRecs: (state, action: PayloadAction<BackendProduct[]>) => {
            state.products = action.payload;
        },
        setRecSessionId: (state, action: PayloadAction<string>) => {
            state.recSessionId = action.payload;
        }
    },
});

// Action creators are generated for each case reducer function
export const { pushToRecs, popFromRecs, setRecs, setRecSessionId } = recProductSlice.actions;
// You must export the reducer as follows for it to be able to be read by the store.
export default recProductSlice.reducer;