import {Box, Slide, Typography, Fade} from "@mui/material";
import Footer from "./Footer";
import {BackendProduct} from "../../types/custom-types/BackendProduct";


interface ProductCardBackProps {
    product: BackendProduct
    showBack: boolean
}

export default function ProductCardBack(props: ProductCardBackProps) {
    return (
        <Fade in= {props.showBack} mountOnEnter unmountOnExit>
            <Box sx={{
                position: "absolute",
                zIndex: "99",
                // backgroundColor: 'rgb(107, 174, 242,0.79)',
                backgroundColor: "rgb(20,46,78,0.79)",
                width: "100%",
                height: "100%",
                display: 'flex',
                flexDirection: 'column',
            }}>
                <Box
                    sx={{
                        display: 'flex',
                        height: '100%',
                        flexDirection: 'column',
                        justifyContent: 'space-between'
                    }}>
                    <Box sx={{
                        margin: '2rem 0.5rem',
                        width: '130px',
                        whiteSpace: "normal"
                    }}>
                        <Typography variant={'body1'}
                                    sx={{
                                        maxHeight: '5rem',
                                        overflow: "hidden"
                                    }}>
                            {(props.product.name.length <= 35) ? props.product.name.toLowerCase() : props.product.name.toLowerCase().substring(0, 34) + "..."}
                        </Typography>
                        <Typography variant={'body1'}
                                    sx={{
                                        fontWeight: '180',
                                        marginTop: '0.5rem'
                                    }}>
                            {props.product.brand}
                        </Typography>
                        <Typography variant={'body1'}
                                    sx={{
                                        fontSize:'0.9rem',
                                        fontWeight: '360',
                                        marginTop: '0.5rem'
                                    }}>
                            {"Size:" + props.product.size}
                        </Typography>
                    </Box>
                    <Footer product={props.product} showBack={true}/>
                </Box>

            </Box>
        </Fade>

    )
}