import {Box, Icon, IconButton} from "@mui/material";
import React from "react";
import HomeIcon from '@mui/icons-material/Home';
import SearchRoundedIcon from '@mui/icons-material/SearchRounded';
import FavoriteIcon from '@mui/icons-material/Favorite';
import PersonIcon from '@mui/icons-material/Person';
import {useDispatch, useSelector} from "react-redux";
import {switchPage} from "../redux/slices/pageSlice";
import {RootState} from "../redux/store";
import {BackendProduct} from "../types/custom-types/BackendProduct";
import {setUserLiked} from "../redux/slices/userSlice";

/**
 * NavBar component displays the navigation bar on the left side of the extension.
 * It uses the page value in the redux store to determine which page to display.
 * @returns 
 */

export default function NavBar() {
    const dispatch = useDispatch();
    const iconSize = '32px'
    const selectedColor = '#142E4E'
    const regularColor =  "#DBE0E4"
    const page: number = useSelector(
        (state: RootState) => state.page.value
    );




    return (
        <Box component="nav" sx={{
            width: "72px",
            display:'flex',
            flexDirection:'column',
            justifyContent: 'space-evenly',
            alignItems: 'center',
        }}>
            <IconButton
                onClick={() => {
                    dispatch(switchPage(0));
                }}>
                <SearchRoundedIcon sx={{
                    fontSize: iconSize,
                    color: (page == 0) ? selectedColor : regularColor
                }}/>
                
            </IconButton>
            
            <IconButton
                onClick={() => {
                    dispatch(switchPage(1));
                }}>
                <FavoriteIcon sx={{
                    fontSize: iconSize,
                    color: (page == 1) ? selectedColor : regularColor
                }}/>
            </IconButton>
            <IconButton
                onClick={() => {
                    dispatch(switchPage(2));
                }}>

                <PersonIcon sx={{
                    fontSize: iconSize,
                    color: (page == 2) ? selectedColor : regularColor
                }}/>
            </IconButton>


        </Box>
    )
}