/**
 * This is a helper method I found on stack overflow. It makes elmnt draggable
 * @param elmnt The element that you want to make draggable
 */
export function dragElement(elmnt: HTMLElement) {
    var pos1 = 0, pos2 = 0, pos3 = 0, pos4 = 0;
    if (document.getElementById(elmnt.id + "header")) {
        // if present, the header is where you move the DIV from:
        document.getElementById(elmnt.id + "header")!.onmousedown = dragMouseDown;
    } else {
        // otherwise, move the DIV from anywhere inside the DIV:
        elmnt.onmousedown = dragMouseDown;
    }

    function dragMouseDown(e: any) {
        e = e || window.event;
        e.preventDefault();
        // get the mouse cursor position at startup:
        pos3 = e.clientX;
        pos4 = e.clientY;
        document.onmouseup = closeDragElement;
        // call a function whenever the cursor moves:
        document.onmousemove = elementDrag;
    }

    function elementDrag(e: any) {
        e = e || window.event;
        e.preventDefault();
        // calculate the new cursor position:
        pos1 = pos3 - e.clientX;
        pos2 = pos4 - e.clientY;
        pos3 = e.clientX;
        pos4 = e.clientY;
        // set the element's new position:

        let topPostDrag = elmnt.offsetTop - pos2
        let leftPostDrag = elmnt.offsetLeft - pos1

        // TODO: set boundaries here!!!!
        if (topPostDrag < 0) {
            topPostDrag = 0
        } else if (topPostDrag > window.innerHeight - elmnt.offsetHeight) {
            topPostDrag = window.innerHeight - elmnt.offsetHeight
        }

        if (leftPostDrag < 0) {
            leftPostDrag = 0
        } else if (leftPostDrag > window.innerWidth - elmnt.offsetWidth) {
            leftPostDrag = window.innerWidth - elmnt.offsetWidth
        }

        // elmnt.style.top = (elmnt.offsetTop - pos2) + "px";
        // elmnt.style.left = (elmnt.offsetLeft - pos1) + "px";
        elmnt.style.top = topPostDrag + "px";
        elmnt.style.left = leftPostDrag + "px";
    }

    function closeDragElement() {
        // stop moving when mouse button is released:
        document.onmouseup = null;
        document.onmousemove = null;
    }
}
