import { Box, Button, FormControl, Input, TextareaAutosize, TextField, TextFieldProps } from "@mui/material";
import { ChangeEvent, ChangeEventHandler, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "../redux/store";
import { UserInfo } from "../types/custom-types/UserInfo";



/**
 * Form component displays a feedback form for the user to submit.
 * @returns 
 */
export default function Form() {
    const dispatch = useDispatch();
    const userInfo: UserInfo | null = useSelector(
        (state: RootState) => state.userInfo.userInfo
    );
    const [submit, setSubmit] = useState(false);
    const [name, setName] = useState((userInfo) ? userInfo!.name : "");
    const [email, setEmail] = useState((userInfo) ? userInfo!.email : "");
    const [message, setMessage] = useState("")

    const nameInputRef = useRef<HTMLElement>();
    const emailInputRef = useRef<HTMLElement>();
    const messageInputRef = useRef<HTMLElement>();



    const onSubmit = () => {
        console.log("Submitting form");
        setSubmit(true);
        chrome.runtime.sendMessage({
            message: "submit-feedback",
            formData: {
                name: name,
                email: email,
                message: message
            }
        }, (response) => {
            console.log(`Send feedback response : ${response}`);
            setName((userInfo) ? userInfo!.name : "");
            setEmail((userInfo) ? userInfo!.email : "");
            setMessage("")
        })
    }
    const onNameChange = (event: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {

        setName(event.target.value)
    }
    const onEmailChange = (event: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        setEmail(event.target.value);
    }
    const onMessageChange = (event: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        setMessage(event.target.value);
    }

    return (
        <Box
            sx={{
                width: "100%",
                height: "100%",
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
                justifyContent: "center"
            }}>
            {submit ?
                (
                    <Button onClick={() => { setSubmit(false) }}>
                        Submit another feedback form
                    </Button>
                )
                :
                (

                    <Box component={'form'} sx={{ width: "100%" }}>
                        <Box sx={{
                            marginTop:"0.5rem",
                            display: "flex",
                            flexDirection: "row",

                        }}>
                            <TextField inputRef={nameInputRef} onClick={() => { nameInputRef.current?.focus() }} label="Full Name" fullWidth
                                value={name} onChange={onNameChange}
                            />
                            <TextField inputRef={emailInputRef} onClick={() => { emailInputRef.current?.focus() }} label="Email" fullWidth
                                value={email} onChange={onEmailChange}
                            />
                        </Box>

                        <TextField inputRef={messageInputRef} placeholder={"Bugs/Suggestions/Say hi to our developers?"} onClick={() => { messageInputRef.current?.focus() }} label="Message" fullWidth multiline rows={4}
                            value={message} onChange={onMessageChange}
                            sx={{
                                marginTop: "0.5rem",
                                marginBottom: "0.5rem"
                            }} />
                        <Button variant="contained"
                            sx={{
                                fontSize: '16px',
                                color: "white",
                                textTransform: "none",
                            }}
                            onClick={onSubmit}
                            >Submit</Button>
                    </Box>
                )
            }
        </Box >
    )
}