import { Box, Typography } from "@mui/material";
import ProductCard from "./ProductCard/ProductCard";
import { BackendProduct } from "../types/custom-types/BackendProduct";


interface ProductListProps {
    products: BackendProduct[]
}

/**
 * ProductList component displays a list of products
 * @param props ProductListProps contains a list of products to display
 * @returns 
 */
export default function ProductList(props: ProductListProps) {

    return (
        <Box
            sx={{
                float: "left",
                textAlign: "left",
                height: '90%',
                width: '95%',
                backgroundColor: '#F4F4F4',
                borderRadius: 5,
                overflow: 'auto',
                //is there a way to make vertical scroll horizontal here?
                whiteSpace: 'nowrap',
            }}>
            <Box
                sx={{
                    marginTop: '9px',
                    marginLeft: '18px',
                    marginRight: '4.5px'
                }}>
                <Box
                    sx={{
                        marginTop: '10px',
                        marginLeft: '20px',
                    }}>
                    {props.products.map(product =>
                        <ProductCard
                            product={product} />)
                    }
                </Box>
            </Box>
        </Box>
    )
}