import Box from "@mui/material/Box";
import { BackendProduct } from "../types/custom-types/BackendProduct";
import { ThreeDots } from 'react-loader-spinner';
import ProductList from "./ProductList";
import { useEffect, useState } from "react";
import ProductFilter from "./ProductFilter";
import { Filter } from "../types/custom-types/Filter";

interface RecPageProps {
    recProducts: BackendProduct[];
}

const recommendationFilterRange = (products: BackendProduct[]) => {
    const prices = products.map(product => product.price);
    const sizes = products.map(product => product.size);
    return ({
        priceRangeLow: Math.min(...prices),
        priceRangeHigh: Math.max(...prices),
        sizes: sizes
    })
}

const applyFilter = (products: BackendProduct[], filter: Filter) => {
    return products.filter(product => {
        return product.price >= filter.priceRangeLow
            && product.price <= filter.priceRangeHigh
            && filter.sizes.includes(product.size)
    })
}



export default function RecPage(props: RecPageProps) {
    const filterRange: Filter = recommendationFilterRange(props.recProducts);
    const [filter, setFilter] = useState<Filter>(filterRange);
    return (
        <Box
            sx={{
                width: '100%',
                height: "100%",
                borderRadius: 5,
                position: 'relative',
                display: "flex",
                justifyContent: "center",
                alignItems: "center",

            }}>
            <ProductList products={applyFilter(props.recProducts, filter)} />
            {/* <ProductFilter filterRange={filterRange} filter={filter} setFilter={setFilter} /> */}
        </Box>
    )
}