import { Box, Button, Typography } from "@mui/material";

import { setUserLiked } from "../redux/slices/userSlice";

import GoogleButton from 'react-google-button'
import { useState, useEffect } from "react";
import { UserInfo } from "../types/custom-types/UserInfo";
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "../redux/store";
import { setUserInfo } from "../redux/slices/userInfoSlice";
import Form from "./Form";

/**
 * UserPage component displays the user's profile picture, name, email, and a feedback form.
 * @returns 
 */
export default function UserPage() {

    // Get the user info from the redux store
    const dispatch = useDispatch();
    const userInfo: UserInfo | null = useSelector(
        (state: RootState) => state.userInfo.userInfo
    );


    // Handle login and set the user info in the redux store
    const handleLogin = () => {
        chrome.runtime.sendMessage({
            message: "login"
        }, (response) => {
            const userInfo = response as UserInfo;
            dispatch(setUserInfo(userInfo));

        })
    }

    // Handle logout and set the user info in the redux store to null
    const handleLogout = () => {
        chrome.runtime.sendMessage({
            message: "logout"
        }, (success) => {
            if (success) {
                dispatch(setUserInfo(null));
            } else {
                console.log("Error: Logout unsuccessful");
            }
        })
    }

    // If the user is not logged in, display the login button
    if (userInfo == null) {
        return (
            <Box
                sx={{
                    display: "flex",
                    width: "100%",
                    height: "100%",
                    justifyContent: "center",
                    alignItems: "center",
                }}>
                <GoogleButton
                    onClick={handleLogin}
                />
            </Box>
        )
    } else {
        // If the user is logged in, display the user's profile picture, name, email, and a feedback form
        return (
            <Box
                sx={{
                    height: '90%',
                    width: '95%',
                    backgroundColor: '#F4F4F4',
                    borderRadius: 3,
                    display: "flex",
                    flexDirection: "row"
                }}>
                <Box
                    sx={{
                        width: "35%",
                        height: "100%",
                        display: "flex",
                        flexDirection: "column",
                        justifyContent: "center",
                        alignItems: "center"
                    }}>
                    <Box
                        component={'img'}
                        src={userInfo.profile_pic}
                        alt="User profile picture"
                        sx={{
                            borderRadius: "50%",
                            width: '100px',
                            height: '100px'
                        }}
                    />
                    <Typography
                        sx={{
                            fontSize: '14.5px',
                            color: "#000000",
                            fontWeight: "bold"
                        }}>
                        {userInfo.name}
                    </Typography>
                    <Typography
                        sx={{
                            fontSize: '14.5px',
                            color: "#000000",
                        }}>
                        {userInfo.email}
                    </Typography>

                    <Button
                        variant="contained"
                        sx={{
                            marginTop: "18px",
                            fontSize: '14px',
                            color: "white",
                            textTransform: "none",
                        }}
                        onClick={handleLogout}>
                        Log Out
                    </Button>
                </Box>
                <Box
                    sx={{ width: "60%", height: "100%" }}>
                    <Form />
                </Box>



            </Box >
        )
    }

}