import { configureStore } from "@reduxjs/toolkit";
// This is how you import a reducer, based on the prior export.
import counterReducer from "./slices/counterSlice";
import productListReducer from "./slices/recProductSlice";
import userReducer from "./slices/userSlice";
import pageReducer from "./slices/pageSlice";
import userInfoReducer from "./slices/userInfoSlice";
const store = configureStore({
    reducer: {
        // You are free to call the LHS what you like, but it must have a reducer on the RHS.
        counter: counterReducer,
        recProducts: productListReducer,
        // user is liked products
        user:userReducer,
        page: pageReducer,
        userInfo: userInfoReducer,
    },
});

export default store;

export type RootState = ReturnType<typeof store.getState>;