import InitialState, {UpdateUserAction} from '../../types/redux/user';
import {createSlice, PayloadAction} from "@reduxjs/toolkit";
import {BackendProduct} from "../../types/custom-types/BackendProduct";

const initialState: InitialState = {
    likedProducts: []
};

export const userSlice = createSlice({
    name: UpdateUserAction,
    initialState: initialState,
    reducers: {
        pushToUserLiked: (state, action: PayloadAction<BackendProduct>) => {
            state.likedProducts.push(action.payload);
        },
        popFromUserLiked: (state, action: PayloadAction<BackendProduct>) => {
            state.likedProducts = state.likedProducts.filter((product:BackendProduct) => product.id != action.payload.id);
        },
        setUserLiked: (state, action: PayloadAction<BackendProduct[]>) => {
            state.likedProducts = action.payload;
        }
    },
});

// Action creators are generated for each case reducer function
export const {
    pushToUserLiked,
    popFromUserLiked,
    setUserLiked
} = userSlice.actions;
// You must export the reducer as follows for it to be able to be read by the store.
export default userSlice.reducer;