import { Box, IconButton, ThemeProvider } from "@mui/material";
import { cardTheme } from "./cardTheme";

import React, { useState } from "react";
import ProductCardBack from "./ProductCardBack";
import Footer from "./Footer";
import FavoriteIcon from '@mui/icons-material/Favorite';
import { BackendProduct } from "../../types/custom-types/BackendProduct";
import { RootState } from "../../redux/store";
import { useDispatch, useSelector } from "react-redux";
import { popFromUserLiked, pushToUserLiked } from "../../redux/slices/userSlice";
import { Event } from "@mui/icons-material";
import { UserInfo } from "../../types/custom-types/UserInfo";
import { switchPage } from "../../redux/slices/pageSlice";

interface ProductCardProps {
    product: BackendProduct
}


/**
 * ProductCard component displays the product information. It has a front and back side.
 * @param props 
 * @returns 
 */

export default function ProductCard(props: ProductCardProps) {

    const dispatch = useDispatch();

    const userInfo: UserInfo | null = useSelector(
        (state: RootState) => state.userInfo.userInfo
    );
    // recSessionId is used to log the click_product event
    const recSessionId: string = useSelector(
        (state: RootState) => state.recProducts.recSessionId
    )
    // userLikedProducts is used to determine if the product is liked or not
    const userLikedProducts: BackendProduct[] = useSelector(
        (state: RootState) => state.user.likedProducts
    )
    let isLiked = userLikedProducts.map(product => product.id).includes(props.product.id);
    const productIsLiked = () => {
        if (userInfo == null) {
            // If the user is not logged in, we need to redirect the user to the login page, and also display a message to the user
            dispatch(switchPage(2));
        }
        else {
            // If the user is logged in, we need to update the userLikedProducts in the redux store when the user clicks the like button
            if (isLiked == true) {
                // If the product is already liked, we need to remove it from the userLikedProducts
                dispatch(popFromUserLiked(props.product));
                chrome.runtime.sendMessage({
                    message: "delete-liked-product",
                    payload: props.product.id
                })
            } else {
                // If the product is not liked, we need to add it to the userLikedProducts
                dispatch(pushToUserLiked(props.product));
                chrome.runtime.sendMessage({
                    message: "insert-liked-product",
                    payload: props.product.id
                })

            }
        }

    }

    const handleClick = () => {
        if(drag == false){
            chrome.runtime.sendMessage({
                message: "click-product",
                payload: {
                    "log_type": "click_product",
                    "rec_id": recSessionId,
                    "product_id": props.product.id
                },
                urls: {
                    "product_url": props.product.link,
                    "referrer_url": window.location.href
                }
            })}
    }

    const [showBack, setShowBack] = useState(false);
    
    //doensn't work...
    const handleLeave = () => {
        setTimeout(()=> {
            setShowBack(false)
        },200)
    };
    //need to create a function that states that if the cursor is over the liked button, then it shouldn't open a new page

    //handles drag and click issue
    let drag = false;
    document.addEventListener(
        'mousedown', () => drag = false);
      
    document.addEventListener(
        'mousemove', () => drag = true);

    
    return (
        <ThemeProvider theme={cardTheme}>

            <Box
                onClick={handleClick}
                onMouseOver={() => setShowBack(true)}
                onMouseOut={() => setShowBack(false)}
                sx={{
                    position: 'relative',
                    backgroundColor: '#ffffff',
                    display: "inline-block",
                    overflow: "hidden",
                    //here changed from 250 to 230
                    height: "225px",
                    width: '153px',
                    marginRight: '9px',
                    borderRadius: 3,
                    boxShadow: "0px 4px 3px rgba(0, 0, 0, 0.15)",
                }}>

                <IconButton
                    sx={{
                        position: 'absolute',
                        zIndex: '999',
                        top: 1,
                        right: 3,
                    }}

                    onClick={(e) => {
                        productIsLiked();
                        //added so that the card wouldn't click
                        e.stopPropagation();
                    }}>
                    <FavoriteIcon sx={{
                        fontSize: '1.5rem',
                        color: isLiked ? 'rgb(233,146,146)' : 'rgb(168,163,163)'
                    }} />
                </IconButton>

                {showBack &&
                    <ProductCardBack product={props.product} showBack={showBack} />}
                <Box
                    sx={{
                        display: 'flex',
                        height: '100%',
                        flexDirection: 'column',
                        justifyContent: 'space-between'
                    }}>

                    <Box
                        sx={{
                            width: '100%',
                            overflow: "hidden",
                            //made a change here: David
                            maxHeight: '200px'
                        }}>


                        <Box
                            component={'img'}
                            sx={{
                                //change here used to be width: 100%
                                width: '153px',
                                height: '178px',
                                objectFit: 'cover'
                            }}
                            src={(Array.isArray(props.product.imgPath)) ? props.product.imgPath[0] : props.product.imgPath}
                        />
                    </Box>

                    <Footer product={props.product} showBack={false} />
                </Box>

            </Box>
        </ThemeProvider>
    )
}