import React, { useEffect } from 'react';
import { Box, IconButton, ThemeProvider } from "@mui/material";
import theme from "./theme";
import { BackendProduct } from "./types/custom-types/BackendProduct";
import NavBar from "./components/NavBar";
import ProductList from "./components/ProductList";
import { useDispatch, useSelector } from "react-redux";
import { RootState } from "./redux/store";
import UserPage from "./components/UserPage";
import CloseIcon from '@mui/icons-material/Close';

import { UserInfo } from './types/custom-types/UserInfo';
import { setUserInfo } from "./redux/slices/userInfoSlice";
import { setUserLiked } from './redux/slices/userSlice';
import RecPage from './components/RecPage';
import LoadingScreen from './components/LoadingScreen';
import ProductFilterPage from './components/ProductFilterPage';
import LikedPage from './components/LikedPage';


/**
 * The root component of the extension
 * @constructor
 */
function App() {

    /**
     * When the component is mounted, we send a message to the background script to check if the user is logged in.
     * If so, we set the user info in the redux store, and we send a message to get the user's liked products.
     */
    useEffect(() => {
        chrome.runtime.sendMessage({
            message: "is-logged-in"
        }, (response) => {
            if (response != null) {
                const userInfo = response as UserInfo;
                dispatch(setUserInfo(userInfo));
            }
        })

        chrome.runtime.sendMessage({
            message: "liked-products"
        }, (response) => {
            if (response != null) {
                const likedProducts = response as BackendProduct[];
                dispatch(setUserLiked(likedProducts));
            }
        })
    }, []);

    //before getProducts.js:46 getting recs ... 


    const recProducts: BackendProduct[] = useSelector(
        (state: RootState) => state.recProducts.products
    );
    const userLikedProducts: BackendProduct[] = useSelector(
        (state: RootState) => state.user.likedProducts
    )
    const page: number = useSelector(
        (state: RootState) => state.page.value
    );

    const dispatch = useDispatch();




    return (
        <ThemeProvider theme={theme}>
            <div className="App">
                <Box sx={{
                    display: "flex",
                    cursor: "pointer",
                    width: "720px",
                    height: "270px",
                    position: "absolute",
                    zIndex: 999,
                    backgroundColor: "#FFFFFF",
                    border: "1px solid #d3d3d3",
                    textAlign: "center",
                    top: 0,
                    right: 0,
                    borderRadius: 5,
                    boxShadow: '0px 1px 1px 1px rgba(0, 0, 0, 0.1), 0px 5px 5px 2px rgba(0, 0, 0, 0.1)'
                }}>
                    <Box sx={{ position: 'absolute' }}>
                        <IconButton
                            onClick={() => {
                                document.getElementById('sift-extension-container')!.style.display = 'none';
                            }}>
                            <CloseIcon sx={{
                                fontSize: '13.5x',
                            }} />
                        </IconButton>
                    </Box>
                    <NavBar />
                    <Box sx={{
                        display: 'flex',
                        alignItems: 'center',
                        width: "648px",
                    }}>
                        {page == 0 && (
                            (recProducts.length == 0) ? <LoadingScreen /> : <RecPage recProducts={recProducts} />
                        )}
                        {page == 1 && (
                            <LikedPage likedProducts={userLikedProducts} />
                        )}
                        {page == 2 && (
                            <UserPage />
                        )}
                    </Box>
                </Box>
            </div>
        </ThemeProvider>
    );
}

export default App;
